header {
  width: 100vw;
  height: 300px;
  position: relative; }
  header:before {
    content: '';
    z-index: 10;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover, transparent 0%, transparent 36%, rgba(0, 0, 0, 0.65) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: -webkit-radial-gradient(center, ellipse, transparent 0%, transparent 36%, rgba(0, 0, 0, 0.65) 100%);
    background: -o-radial-gradient(center, ellipse, transparent 0%, transparent 36%, rgba(0, 0, 0, 0.65) 100%);
    background: radial-gradient(ellipse at center, transparent 0%, transparent 36%, rgba(0, 0, 0, 0.65) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000', GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */
    opacity: 0.6; }
  header:after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #000; }

.header-img1 {
  background: url("/header1.jpg") center center;
  background-size: cover;
  position: absolute;
  width: 100vw;
  height: 300px; }

.header-img2 {
  background: url("/header2.jpg") center center;
  background-size: cover;
  position: absolute;
  width: 100vw;
  height: 300px; }

.header-img3 {
  background: url("/header3.jpg") center center;
  background-size: cover;
  position: absolute;
  width: 100vw;
  height: 300px; }

.bk-enter-active,
.bk-exit-active {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease; }

.bk-enter,
.bk-leave-to {
  -webkit-transform: scale(1.1) translateZ(0);
          transform: scale(1.1) translateZ(0);
  opacity: 0; }

.bk-img {
  position: absolute;
  width: 100vw;
  height: 300px;
  overflow: hidden;
  top: 0; }

.nav-wrapper {
  width: 100vw;
  position: relative;
  z-index: 1000;
  background: rgba(4, 67, 98, 0.25); }

@media screen and (max-width: 1030px) {
  .nav-wrapper {
    padding: 0 20px; } }

ul {
  list-style: none;
  padding: 15px 0; }
  ul li {
    display: inline-block;
    margin-right: 40px; }
  ul a,
  ul a:active,
  ul a:visited {
    color: white;
    text-decoration: none; }

@media screen and (max-width: 600px) {
  ul {
    display: none; } }

.nuxt-link-active {
  font-weight: bold; }

nav {
  max-width: 1000px;
  margin: 0 auto;
  position: relative; }

.menu {
  position: absolute;
  right: 0;
  top: 8px;
  cursor: pointer; }



