  grid-gap: 10px;
  grid-template-columns: repeat(4, 24%);
  grid-template-rows: repeat(3, 120px);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: end;
      align-content: end; }

